<template>
    <v-container fluid>
        <v-card>
            <v-card-title class="green darken-3">
                <div class="mx-auto white--text">
                    <v-icon class="white--text">fa-tools</v-icon>
                    <span class="display-1 font-weight-light">Skills</span>
                </div>
            </v-card-title>
            <v-card-text>
                <v-layout row wrap justify-space-around>
                    <v-flex 
                    md3 
                    sm6 
                    xs12
                    v-for="skill in skills"
                    :key="skill.title"
                    >
                        <template>
                            <v-hover>
                                <v-card 
                                class="text-xs-center skill-card ma-3 evaluation-2"
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 4}`"
                                :color="skill.background">
                                    <v-card-text>
                                        <div>
                                            <v-progress-circular
                                            :value="skill.value"
                                            :color="skill.color"
                                            :rotate="360"
                                            width="15"
                                            size="100"
                                            >
                                            {{skill.value}}
                                            </v-progress-circular>
                                        </div>
                                        <div class="headline" >
                                            <v-tooltip bottom>
                                                <v-btn outline 
                                                slot="activator"
                                                :color="skill.color" 
                                                :class="`${skill.color}--text`"
                                                >
                                                <v-icon small>fab fa-node-js</v-icon>{{skill.title}}
                                                </v-btn>
                                                <span color="white">{{skill.title}}</span>
                                            </v-tooltip>
                                        </div>
                                    </v-card-text>
                                </v-card>
                            </v-hover>
                        </template>
                    </v-flex>
                </v-layout>
            </v-card-text>
        </v-card>
    </v-container>
</template>

<script>
export default {
    data() {
        return {
            skills: [
                {title:"Javascript", value:0, absoluteValue: 80, color:"indigo", background: "#d6f6ff"},
                {title:"React", value:0, absoluteValue: 80, color:"white", background: "#0894a1"},
                {title:"Node.js", value:0, absoluteValue: 80, color:"#424242", background: "#00EEFA"},
                {title:"Vue.js", value:0, absoluteValue: 80, color:"red", background: "#68EFBF"},
                {title:"Flutter", value:0, absoluteValue: 80, color:"blue", background: "#F2EFDF"},
                {title:"MongoDB", value:0, absoluteValue: 80, color:"#000000", background: "#F5DFFB"},
                {title:"MySQL", value:0, absoluteValue: 80, color:"cyan", background: "#1B1A28"}
            ],
            interval: {}
        }
    },
    beforeDestroy () {
      clearInterval(this.interval)
    },
    mounted () {
      var timesRun = 0;
      this.interval = setInterval(() => {
          if(timesRun === 10) {
              clearInterval(this.interval);
              return;
          }
          timesRun += 1;
        this.skills.forEach((skill) => {
            if(skill.absoluteValue !== skill.value) {
                skill.value += 10
            }
        });
      }, 300)
    }
}
</script>

